const SEND_NEW_MSG = 'dialogs/SEND-NEW-MSG';

const initialState = {
  dialogItems: [
    {
      id: 1,
      name: '',
      actived: true
    },
    {
      id: 2,
      name: ''
    },
    {
      id: 3,
      name: ''
    },
    {
      id: 4,
      name: ''
    },
    {
      id: 5,
      name: ''
    },
    {
      id: 6,
      name: ''
    },
    {
      id: 7,
      name: ''
    },
  ],

  messages: [
    {
      id: 1,
      name: '',
      text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Esse, ad?',
      responsedMsg: false
    },
    {
      id: 2,
      name: '',
      text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Esse, ad?',
      responsedMsg: true
    },
    {
      id: 3,
      name: '',
      text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Esse, ad?',
      responsedMsg: false
    },
    {
      id: 4,
      name: '',
      text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Esse, ad?',
      responsedMsg: true
    },
  ],

}

const dialogsReducer = (state = initialState, action) => {

  switch (action.type) {
      
    case SEND_NEW_MSG: {
      // If text is empty we should break function;
      if(action.text === '') return state;

      const msg = {
        id: Math.random(),
        name: '',
        text: action.text,
        responsedMsg: true
      };

      return {
        ...state,
        messages: [...state.messages, msg]
      };

    }
      
    default:
      return state;
  }
};

export const sendNewMsgActionCreator = (text) => {
  return {
    type: SEND_NEW_MSG,
    text: text
  };
};

export default dialogsReducer;