import { Field } from 'react-final-form';
import styles from './FormControl.module.css'


export const InputArea = ({input, meta: {error, touched}, ...props}) => {
  return (
    <div>
      <input {...input} {...props} className={error && touched ? styles.error : ''} />
      {error  && touched && <div className={styles.errorText}>{error}</div>}
    </div>
  )
};

export const Textarea = ({input, meta: {error, touched}, ...props}) => {
  return (
    <div>
      <textarea {...input} {...props} className={error && touched ? styles.error : ''}></textarea>
      {error && touched && <div className={styles.errorText}>{error}!!!</div>}
    </div>
  )
};

export const createField = (email, validators, component, props, text = '') => {
  return (
    <>
      <Field 
        name={email} 
        validate={validators} 
        component={component} 
        {...props}
      /> {text}
    </>
  )
}