import classes from './FriendsList.module.css';
import { NavLink } from 'react-router-dom';

const FriendsList = ( {activeFriends} ) => {
  return (
    <ul className={classes.friendsList}>
      {
        activeFriends.map((item, index) => {
          return (
            <li className={classes.friendsListItem} key={index}>
              <NavLink to="/dialogs/" className={classes.friendsLink}>
                <div className={classes.friendsAva}></div>
                {item.name}
              </NavLink>
            </li>
          )
        })
      }
    </ul>
  );
};

export default FriendsList;