import { AUTH_API } from "../api/api";

const SET_USER_DATA = 'auth/SET_USER_DATA';
const SET_AJAX_ERROR_MSG = 'auth/SET_AJAX_ERROR_MSG';

const initialState = {
  userId: null,
  login: null,
  email: null,
  isAuth: false,
  ajaxErrorMsg: ''
};

const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_USER_DATA: 
      return {
        ...state,
        ...action.data
      }

    case SET_AJAX_ERROR_MSG: 
      return {
        ...state,
        ajaxErrorMsg: action.message
      }

    default:
      return state;
  }
};

export const setAuthUserData = (userId, email, login, isAuth = true) => {
  return {
    type: SET_USER_DATA,
    data: {userId, email, login, isAuth}
  }
};

export const setAJAXErrorMsg = (msg) => {
  return {
    type: SET_AJAX_ERROR_MSG,
    message: msg
  }
}

export const getAuthUserThunkCreator = () => {
  return async (dispatch) => {
    const res = await AUTH_API.getAuthUser()
    if(res.data.resultCode === 0) {
      const {login, email, id} = res.data.data;
      dispatch(setAuthUserData(id, email, login));
    }
  }
} 

export const loginThunkCreator = ({email, password, rememberMe = false}) => {
  return async (dispatch) => {
    const res = await AUTH_API.postAuthUser(email, password, rememberMe);
    if(res.data.resultCode === 0) {
      dispatch(getAuthUserThunkCreator());
      dispatch(setAJAXErrorMsg(''));
    } else {
      dispatch(setAJAXErrorMsg(res.data.messages[0]))
    }
  }
} 

export const logoutThunkCreator = () => {
  return async (dispatch) => {
    const res = await AUTH_API.deleteAuthUser();
    if(res.data.resultCode === 0) {
      dispatch(setAuthUserData(null, null, null, false));
    }
  }
} 

export default authReducer;