import './Users.css'
import Paginator from '../common/Paginator/Paginator';
import User from '../User/User';


const Users = ({totalUsersCount, pageSize, currentPage, changeCurrentPage, users, followingProgress, follow, unfollow}) => {      
  return (
    <div className='users'>
      <h2>Users</h2>

      <Paginator totalItemsCount={totalUsersCount} pageSize={pageSize} currentPage={currentPage} changeCurrentPage={changeCurrentPage}/>

      <div className='usersList'>
        {users.map(user => <User key={user.id} user={user} followingProgress={followingProgress} follow={follow} unfollow={unfollow}/>)}
      </div>

      <button className="usersShowMore">Show more</button>
    </div>
  )
}

export default Users;