import './User.css'
import { NavLink } from "react-router-dom"

const User = ({user, followingProgress, follow, unfollow}) => {
  return (
    <div className="user">
      <div className="userInteractive">
        <NavLink to={'/profile/' + user.id}>
          <div className="userAvatar"></div>
        </NavLink>
        {user.followed 
          ? <button disabled={followingProgress.some(id => id === user.id)} onClick={() => {
            follow(user.id)
        
          }} className="userFollow">Unfollow</button>
          : <button disabled={followingProgress.some(id => id === user.id)} onClick={() => {
              unfollow(user.id)
          }} className="userFollow">Follow</button>}
        
      </div>
      <div className="userInfo">
        <div className="userInfoData">
          <div className="userName">{user.name}</div>
          <div className="userLocation">
            {/* {user.location.country}, <br />
            {user.location.city} */}
            some country
          </div>
        </div>
        <p className="userInfoStatus">{user.status}</p>
      </div>
    </div>
  )
}

export default User;