import { USERS_API } from "../api/api";
import { updateObjectInArray } from "../utils/object-helpers";

const FOLLOW = 'users/FOLLOW';
const UNFOLLOW = 'users/UNFOLLOW';
const SET_USERS = 'users/SET_USERS';
const SET_TOTAL_USERS_COUNT = 'users/SET_TOTAL_USERS_COUNT';
const SET_CURRENT_PAGE_NUMBER = 'users/SET_CURRENT_PAGE_NUMBER';
const SHOW_AND_HIDE_PRELOADER = 'users/SHOW_AND_HIDE_PRELOADER';
const TOGGLE_FOLLOWING_PROGRESS = 'users/TOGGLE_FOLLOWING_PROGRESS';

const initialState = {
  users: [],
  totalUsersCount: 0,
  pageSize: 10,
  currentPage: 1,
  isFetching: true,
  followingProgress: [],
};

// ============= Reducer =============
const usersReducer = (state = initialState, action) => {
  switch (action.type) {
    case FOLLOW: 
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, 'id', {followed: false})
      }

    case UNFOLLOW:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, 'id', {followed: true})
      }

    case SET_USERS: {
      return {
        ...state,
        users: [...action.users]
      }
    }

    case SET_TOTAL_USERS_COUNT: {
      return {
        ...state,
        totalUsersCount: action.count
      }
    }

    case SET_CURRENT_PAGE_NUMBER: {
      return {
        ...state,
        currentPage: action.pageNumber
      }
    }

    case SHOW_AND_HIDE_PRELOADER: {
      return {
        ...state,
        isFetching: action.isFetching
      }
    }

    case TOGGLE_FOLLOWING_PROGRESS: {
      return {
        ...state,
        followingProgress: action.isFetching 
          ? [...state.followingProgress, action.userId] 
          : state.followingProgress.filter(id => id !== action.userId)
      }
    }

    default:
      return state;
  }
};

// ============= Action creators =============
export const follow = (userId) => {
  return {
    type: FOLLOW,
    userId: userId
  }
};

export const unFollow = (userId) => {
  return {
    type: UNFOLLOW,
    userId: userId
  }
};

export const setUsers = (users) => {
  return {
    type: SET_USERS,
    users: users
  }
}

export const setTotalUsersCount = (count) => {
  return {
    type: SET_TOTAL_USERS_COUNT,
    count: count
  }
}

export const setCurrentPageNumber = (pageNumber) => {
  return {
    type: SET_CURRENT_PAGE_NUMBER,
    pageNumber: pageNumber
  }
}

export const showAndHidePreloader = (isResponsed) => {
  return {
    type: SHOW_AND_HIDE_PRELOADER,
    isFetching: isResponsed
  }
}

export const toggleFollowingProgress = (isResponsed, userId) => {
  return {
    type: TOGGLE_FOLLOWING_PROGRESS,
    isFetching: isResponsed,
    userId
  }
}

// ============= Thunk creators =============
export const getUsersThunkCreator = (pageSize, currentPage) => {
  return async (dispatch) => {
    dispatch(showAndHidePreloader(true));

    const data = await USERS_API.getUsers(pageSize, currentPage);
    dispatch(showAndHidePreloader(false));
    dispatch(setUsers(data.items));
    dispatch(setTotalUsersCount(Math.ceil(data.totalCount)));
  }
}



export const followUserThunkCreator = (userId) => {
  return async (dispatch) => {
    followUnfollowFlow(dispatch, userId, USERS_API.deleteUser.bind(USERS_API), follow)
  }
}

export const unfollowUserThunkCreator = (userId) => {
  return async (dispatch) => {
    followUnfollowFlow(dispatch, userId, USERS_API.postUser.bind(USERS_API), unFollow)
  }
}


// Helpers
const followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {
  dispatch(toggleFollowingProgress(true, userId));

  const data = await apiMethod(userId);

  if(data.resultCode === 0) {
    dispatch(actionCreator(userId));
  }
  dispatch(toggleFollowingProgress(false, userId));
};




export default usersReducer;