import { Form, Field } from 'react-final-form';
import { composeValidators, maxLength, requiredField } from '../../../utils/validators';
import { InputArea, Textarea, createField } from '../FormControls/FormControls';
import styles from './MyForms.module.css'

export const LoginForm = ({onSubmit, ajaxErrorMsg}) => {
  return (
    <Form 
      onSubmit={onSubmit}
      render={({handleSubmit, submitting, pristine}) => {
        return (
        <form onSubmit={handleSubmit} className={styles.form}>
          {createField('email', composeValidators(requiredField, maxLength(50)), InputArea, {type: 'text', placeholder: 'Login'})}

          {createField('password', composeValidators(requiredField, maxLength(20)), InputArea, {type: 'password', placeholder: 'Password'})}

          {ajaxErrorMsg ? <div className={styles.errorText}>{ajaxErrorMsg}</div> : ''}

          <div>
            <label>
              {createField('rememberMe', null, 'input', {type: 'checkbox', placeholder: null}, 'remember me')} 
            </label>
          </div>

          <button disabled={submitting || pristine}>Login</button>
        </form>
    )
      }} />
    
  )
}

export const AddMsgForm = ({onSubmit, classes}) => {
  return (
    <Form
      onSubmit={onSubmit} 
      render={({handleSubmit, submitting, pristine}) => (
      <form className={classes.chatInteractive} onSubmit={handleSubmit}>
        <Field 
          component="textarea"
          name="chatTextArea" 
          className={classes.chatTextArea} 
        />

        <button disabled={submitting || pristine} className={classes.chatSendMsgBtn}> </button>
      </form>
    )} />
  )
};


export const PostForm = ({onSubmit}) => {
  return (
    <Form 
        onSubmit={onSubmit}
        render={({handleSubmit, submitting, pristine}) => (
          <form onSubmit={handleSubmit}>
            <Field 
              component={Textarea}
              validate={composeValidators(requiredField, maxLength(50))}
              name="post" 
              id="post-input" 
              cols="90" 
              rows="5"
            />

            <button disabled={submitting || pristine}>Send</button>
          </form>
        )} 
      />
  )
}