import { connect } from "react-redux";
import { setCurrentPageNumber, getUsersThunkCreator, followUserThunkCreator, unfollowUserThunkCreator } from "../../redux/users-reducer";

import React from 'react';
import Users from './Users';

import Prealoder from "../common/Preloader/Preloader";
import { withAuthRedirectHOC } from "../../hoc/withAuthRedirect";
import { compose } from "redux";
import { getCurrentPage, getFollowingProgress, getIsFetching, getPageSize, getTotalUsersCount, getUsers } from "../../redux/selectors/users-selectors";

class UsersAPIConnect extends React.Component {
  componentDidMount() {
    const {getUsers, pageSize, currentPage} = this.props;
    getUsers(pageSize, currentPage)
  }

  changeCurrentPage = (pageNumber) => {
    const {getUsers, pageSize, currentPage, setCurrentPageNumber} = this.props;
    setCurrentPageNumber(pageNumber);
    getUsers(pageSize, currentPage)
  }

  render() {
    const {isFetching} = this.props;
    
    return (
      <>
        {isFetching ? <Prealoder/> : ''}
        <Users {...this.props} changeCurrentPage={this.changeCurrentPage}/>
      </>
    )
  }
}


const mapStateToProps = (state) => {
  return {
    users: getUsers(state),
    pageSize: getPageSize(state),
    totalUsersCount: getTotalUsersCount(state),
    currentPage: getCurrentPage(state),
    isFetching: getIsFetching(state),
    followingProgress: getFollowingProgress(state),
  }
}


const UsersContainer = compose(
  connect(mapStateToProps, {
    setCurrentPageNumber,
    getUsers: getUsersThunkCreator,
    follow: followUserThunkCreator,
    unfollow: unfollowUserThunkCreator
  }),
  withAuthRedirectHOC,
)(UsersAPIConnect);

export default UsersContainer;