import { useState } from 'react';
import './Paginator.css'

const Paginator = ({totalItemsCount, pageSize, currentPage, changeCurrentPage, portionSize = 10}) => {
  const pagesCount = Math.ceil(totalItemsCount / pageSize);
  const pages = [];
  for(let i = 1; i <= pagesCount; i++) {
    pages.push(i);
  }

  const portionCount = Math.ceil(pagesCount / portionSize);
  let [portionNumber, setPortionNumber] = useState(1);
  let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
  let rightPortionPageNumber = portionNumber * portionSize;


  return (
    <div className="pagination">
      {
        portionNumber > 1 && <button className='page-btn paginator-prev-page' onClick={() => {
          setPortionNumber(portionNumber - 1);
          changeCurrentPage(leftPortionPageNumber - portionSize)
        }}></button>
      }

      {
        pages
          .filter(page => page >= leftPortionPageNumber && page <= rightPortionPageNumber)
          .map((page) => {
            return <span className={currentPage === page ? 'active-page' : ''} key={page} onClick={() => {changeCurrentPage(page)}}>{page}</span>
          })
      }

      {
        portionCount > portionNumber && <button className='page-btn paginator-next-page' onClick={() => {
          setPortionNumber(portionNumber + 1);
          changeCurrentPage(leftPortionPageNumber + portionSize)
        }}></button>
      }
    </div>
  )
}

export default Paginator;