import { compose } from 'redux';
import { LoginForm } from '../common/MyForms/MyForms';
import './Login.css';
import { connect } from 'react-redux';
import { loginThunkCreator } from '../../redux/auth-reducer';
import { Navigate } from 'react-router';
import { getAjaxErrorMsg, getIsAuth } from '../../redux/selectors/auth-selectors';


const Login = ({loginThunk, isAuth, ajaxErrorMsg}) => {
  const onSubmit = (values) => {
    loginThunk(values);
  };

  return isAuth ? <Navigate to='/profile'/> : (
    <div className='authorization'>
      <h1>Login</h1>
      <LoginForm onSubmit={onSubmit} ajaxErrorMsg={ajaxErrorMsg} />
    </div>
  )
}

const mapStateToProps = (state) => {
  return {
    isAuth: getIsAuth(state),
    ajaxErrorMsg: getAjaxErrorMsg(state)
  }
}

export default compose(
  connect(mapStateToProps, { loginThunk: loginThunkCreator})
)(Login)