import React from 'react';
import { HashRouter, Route, Routes } from 'react-router-dom';
import './App.css';
import Nav from './components/Nav/Nav';
import News from './components/News/News';
import Music from './components/Music/Music';
import Settings from './components/Settings/Settings';
import UsersContainer from './components/Users/UsersContainer';
import HeaderContainer from './components/Header/HeaderContainer';
import Login from './components/Login/Login';
import { Provider, connect } from 'react-redux';
import { initializeApp } from './redux/app-reducer';
import { compose } from 'redux';
import { withRouter } from './components/common/witRouter';
import Prealoder from './components/common/Preloader/Preloader';
import { getInitialized } from './redux/selectors/app-selectors';
import store from './redux/redux-store';
import { withLazySuspenseHOC } from './hoc/withLazySuspenseHOC';

const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'));
const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'));


class App extends React.Component {
  componentDidMount() {
    this.props.initializeApp()
  }

  render() {
    if(!this.props.initialized) {
      return <Prealoder /> 
    }

    return (
        <div className="App">
          <HeaderContainer />
          <Nav />
          <div className="App-content">
            <Routes>
              <Route path='/dialogs' element={withLazySuspenseHOC(DialogsContainer)}/>
              <Route path='/profile/:userId?' element={withLazySuspenseHOC(ProfileContainer)} />
              <Route path='/news' element={<News />} />
              <Route path='/music' element={<Music />} />
              <Route path='/users' element={<UsersContainer />} />
              <Route path='/settings' element={<Settings />} />
              <Route path='/login' element={withLazySuspenseHOC(Login)} />
            </Routes>
          </div>
        </div>
    ) 
  }
}

const mapStateToProps = (state) => {
  return {initialized: getInitialized(state)}
};

const AppContainer = compose(
  withRouter,
  connect(mapStateToProps, { initializeApp: initializeApp})
)(App);

const MainApp = (props) => {
  return (
    <React.StrictMode>
      <HashRouter>
        <Provider store={store}>
          <AppContainer />
        </Provider>
      </HashRouter>
    </React.StrictMode>
  )
}

export default MainApp;